.gform_wrapper * {
	box-sizing: border-box;
}
.gfield_list_group,
.gfield_list_header {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	margin-bottom: 8px;
}

.gfield_header_item,
.gform_fileupload_rules,
.ginput_complex label {
	font-size: 15px;
	padding-top: 5px;
}

.gfield_header_item,
.gfield_list_group_item {
	flex-grow: 1;
	width: 0;
}

.gfield_header_item + .gfield_header_item:not(:last-child),
.gfield_list_group_item + .gfield_list_group_item {
	margin-left: 2%;
}

.gform_wrapper.gravity-theme .gfield_list_group:last-child {
	margin-bottom: 0;
}
.gfield_header_item:last-child,
.gfield_list_icons {
	width: 8%;
	flex: none;
	display: flex;
	align-items: center;
	justify-content: center;
}

.gfield_list_group_item input,
.gfield_list_group_item select {
	width: 100%;
}

.gfield input,
.gfield select {
	max-width: 100%;
}

.gfield_list_icons button:focus,
.gfield_list_icons button:hover {
	opacity: 1;
	outline: 0;
}

.gfield_list_icons button {
	font-size: 0;
	background: transparent url(../../assets/images/icons/plus.svg);
	background-repeat: no-repeat;
	border: none;
	width: 25px;
	height: 25px;
	opacity: 1;
	padding: 0;
	transition: opacity 0.5s ease-out;
}

.gfield_list_icons button.delete_list_item {
	margin-left: 5px;
	background-image: url(../../assets/images/icons/minus.svg);
}

@media (max-width: 640px) {
	.gfield_header_item,
	.gfield_list_group_item {
		width: auto;
	}

	.gfield_list_header {
		display: none;
	}

	.gfield_list_group:last-child {
		margin-bottom: 0;
	}

	.gfield_list_group {
		display: block;
		border: 1px solid rgba(241, 238, 238, 0.2);
		padding: 16px;
		margin-bottom: 16px;
	}

	.gfield_list_group_item:not(:last-child) {
		margin-bottom: 8px;
	}

	.gfield_header_item + .gfield_header_item:not(:last-child),
	.gfield_list_group_item + .gfield_list_group_item {
		margin-left: 0;
	}

	.gfield_list_group_item:not(:last-child) {
		margin-bottom: 8px;
	}
}

@media only screen and (max-width: 641px) {
	input:not([type="radio"]):not([type="checkbox"]):not([type="image"]):not([type="file"]) {
		line-height: 2;
		min-height: 32px;
	}
}
